 the latter of which will contain the 700th episode.[6] The Simpsons is a joint production by Gracie Films and 20th Century Fox Television and syndicated by 20th Television. ")]),_c('h2',[_vm._v(" And This is more about the Simpsons ")]),_c('p',[_vm._v(" The Simpsons received acclaim throughout its first nine or ten seasons, which are generally considered its \"Golden Age\". Time named it the 20th century's best television series, and Erik Adams of The A.V. Club named it \"television's crowning achievement regardless of format\". On January 14, 2000, the Simpson family was awarded a star on the Hollywood Walk of Fame. It has won dozens of awards since it debuted as a series, including 34 Primetime Emmy Awards, 34 Annie Awards, and a Peabody Award. Homer's exclamatory catchphrase \"D'oh!\" has been adopted into the English language, while The Simpsons has influenced many other later adult-oriented animated sitcoms. However, it has also been criticized for a perceived decline in quality over the years. ")])])])}]

